@import url(<https://fonts.googleapis.com/css2?family=Yellowtail&display=swap>);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{width:100%;height:100%;background-color:#A6979C}body .container{display:flex;flex-direction:column;align-items:center;width:600px;max-width:calc(100% - 20px);margin:0 auto}body .container .title{font-family:'YellowTail', sans-serif;font-size:32pt;color:#42383B}body .container .task-done{text-decoration:line-through}body .container form{width:100%;display:flex}body .container form input[type="text"]{height:30px;width:100%;border:1px solid #ccc;border-radius:5px 0 0 5px;padding:5px;outline-color:#42383B;font-size:14pt}body .container form button{background-color:#42383B;color:#fff;border:0;padding:0 20px;border-radius:0 5px 5px 0;font-size:14pt}body .container table{width:100%;border-collapse:collapse}body .container table td:first-child{table-layout:fixed;width:20px}body .container table td:nth-child(2){padding:10px 20px;font-size:14pt}body .container table tr:nth-child(even){background-color:#8E7B81}

